import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    // constructor(props) {
    //     super(props);
    //     this.state = {
    //         selectedTab: 'blueTab',
    //     };
    // }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                
                position: 'fixed',
                width: '100%',
                bottom: 0,
                zIndex:'9999px'
                
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="purple"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={ 
                            <i className='iconfont icon-biaoqianA01_shouye-31'></i>
                        }                      
                        selectedIcon={ 
                            <i className='iconfont icon-shouye'></i>

                        }                     
                        selected={ pathname === '/home' }
                        onPress={ () => {
                            this.props.history.push('/home');
                        } }
                        data-seed="logId"
                      
                    >
                        
                    </TabBar.Item>
                    <TabBar.Item  
                        title="购物车"
                        key="cart"
                        icon={ 
                            <i className='iconfont icon-gouwuche1'></i>
                        } 
                    
                        
                        selectedIcon={ 
                            <i className='iconfont icon-gouwuche2'></i>
                        }   
                      
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                        
                    >
                        购物车
                    </TabBar.Item>
                    <TabBar.Item
                        title="订单"
                        key="list"
                        icon={ 
                            <i className='iconfont icon-biaoqiankuozhan_dingdan-274'></i>
                        }                    
                        selectedIcon={ 
                            <i className='iconfont icon-dingdan'></i>

                        }   
                        
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')
                        } }
                        data-seed="logId2"
                      
                    >
                        订单
                        
			</TabBar.Item>
                    <TabBar.Item
                        icon={ 
                            <i className='iconfont icon-biaoqianA01_wode-95'></i>
                        }                        
                        selectedIcon={ 
                            <i className='iconfont icon-wode'></i>
                        }   
                        title="我的"
                        key="my"
                        
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        data-seed="logId3"
                        
                    >
                        我的
			</TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);