import React from 'react';
import { Button, TabBar,NavBar,Icon } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './Babytab.css';
class Babytab extends React.Component {
    
    constructor(props, context) {
        super(props, context)
        console.info(props)
        this.userList = props.location.state.data
      }
    render() {
        var userList = {}
        // const { pathname } = this.props.location;
        return (
            <div>
                <NavBar
                    mode="light"
                    icon={ <Icon type="left" /> }
                    style={{position:'fixed',top:'5px',left:'5px',backgroundColor:'black',opacity:'0.5',width:'50px',borderRadius: '50%',height:'50px'}}
                    onLeftClick={ () => {
                        this.props.history.push('/home');
                    } }
                ></NavBar>
                <div style={{paddingBottom:'5px'}}>
                <div >
                <img  style={{width:'100%'}} src={`${this.userList.img}`}></img>
                </div>
                <div style={{paddingBottom:'5px',color: '#ff8800',fontSize: '30px',width: '100%'}}>{this.userList.money}</div>
                <div style={{paddingBottom:'5px',fontSize:'25px'}}>{this.userList.name}</div>
                <div style={{paddingBottom:'5px',fontSize:'20px',color:'#bbb4b4'}}>{this.userList.number}</div>
            </div>
            <div className='bottoms'>
           
                <div className='tables'>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="purple"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        key="homes"
                        icon={ 
                            <i className='iconfont icon-dianpu'></i>
                        }                        
                    >
                    </TabBar.Item>
                    <TabBar.Item  
                        title="客服"
                        key="cart"
                        icon={ 
                            <i className='iconfont icon-kefu'></i>
                        } 
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="收藏"
                        key="list"
                        icon={ 
                            <i className='iconfont icon-shoucang1'></i>
                        }                                         
                    >
			</TabBar.Item>
              </TabBar>
              </div>
              <div class="left">
                  <span class="btn-title">加入购物车</span>
              </div>
                <div class="right">
                    <span class="btn-title">立即购买</span>
                </div>                
            </div>      
            </div>    
           
        );
    }
}
export default withRouter(Babytab);
